@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
html,
body {
  font-family: "Roboto";
  font-weight: 300;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

.date-picker {
  display: grid;
  cursor: default;
  grid-gap: 0;
  gap: 0;
  grid-template-columns: repeat(7, 1fr);
  width: 0;
}

.date {
  margin: 0 0.5em;
  display: flex;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(216, 216, 216);
  border-radius: 100%;
  transition: background-color 0.1s, color 0.15s;
}

.date:hover {
  background-color: rgba(220, 226, 208, 0.349);
}

.date:active {
  background-color: rgba(135, 188, 37, 0.062);
  color: rgb(26, 26, 26);
}

.selected {
  background-color: rgb(134, 188, 37);
  color: white;
}

.selected:hover {
  background-color: rgb(151, 207, 46);
}

